import * as React from 'react'

const wineImage = {
    height: '15rem'
}

export default function Wine() {
    let items = []
    let [wineTitles, setWineTitles] = React.useState([])

    React.useEffect(async () => {
        let res = await fetch('https://api.sampleapis.com/wines/reds')
        let wines = await res.json()
        for (let i = 0; i < wines.length; i++) {
            console.log(wines[i].winery)
            items.push(<li>
                <div style={{ width: "40rem", overflow: "hidden" }} >
                    <div style={{ width: "12rem", float: "left" }}>
                        < img style={wineImage} src={wines[i].image} />
                    </div>
                    <div >
                        <b>{wines[i].winery}</b> -- {wines[i].wine}
                    </div>
                    <div>
                        Rating: Average {wines[i].rating.average} - Review {wines[i].rating.reviews}
                    </div>
                    <div>
                        Location: {wines[i].location}
                    </div>
                </div>
            </li>)
        }
        setWineTitles(items)
    }, [])

    return (
        <>
            <h1>Wines</h1>
            <ul>
                {wineTitles}
            </ul>
        </>
    )
}